<template>
  <div id="home">
    <div id="home-1">
      <div style="font-size:30px">铿锵侠</div>
      <div style="font-size:16px">一名目前在帝都的GISer。</div>
      <div style="font-size:16px;margin-top:30px">知乎专栏</div>
      <Tooltip content="科普为主，适合所有人阅读" placement="right">
        <a style="font-size:16px" href="https://zhuanlan.zhihu.com/mapworld" target="_blank">《地图看世界》</a>
      </Tooltip>
      <br>
      <Tooltip content="技术为主，适合GIS初学者阅读" placement="right">
        <a style="font-size:16px" href="https://zhuanlan.zhihu.com/GISDevelop" target="_blank">《GIS开发从入门到放弃》</a>
      </Tooltip>
      <div style="font-size:16px;margin-top:30px">地图应用</div>
      <div style="font-size:16px">施工中</div>
    </div>
    <canvas id="home-cartoon"></canvas>
  </div>
</template>

<script>
  export default {
    name: 'home',
    data() {
      return {

      }
    },
    mounted() {
      this.init();
    },
    watch: {},
    methods: {
      // 初始化
      init() {
        document.addEventListener('touchmove', function (e) {
          e.preventDefault()
        })
        var c = document.getElementsByTagName('canvas')[0],
          x = c.getContext('2d'),
          pr = window.devicePixelRatio || 1,
          w = window.innerWidth,
          h = window.innerHeight,
          f = 90,
          q,
          m = Math,
          r = 0,
          u = m.PI * 2,
          v = m.cos,
          z = m.random
        c.width = w * pr
        c.height = h * pr
        x.scale(pr, pr)
        x.globalAlpha = 0.6

        function i() {
          x.clearRect(0, 0, w, h)
          q = [{
            x: 0,
            y: h * .7 + f
          }, {
            x: 0,
            y: h * .7 - f
          }]
          while (q[1].x < w + f) d(q[0], q[1])
        }

        function d(i, j) {
          x.beginPath()
          x.moveTo(i.x, i.y)
          x.lineTo(j.x, j.y)
          var k = j.x + (z() * 2 - 0.25) * f,
            n = y(j.y)
          x.lineTo(k, n)
          x.closePath()
          r -= u / -50
          x.fillStyle = '#' + (v(r) * 127 + 128 << 16 | v(r + u / 3) * 127 + 128 << 8 | v(r + u / 3 * 2) * 127 + 128).toString(
            16)
          x.fill()
          q[0] = q[1]
          q[1] = {
            x: k,
            y: n
          }
        }

        function y(p) {
          var t = p + (z() * 2 - 1.1) * f
          return (t > h || t < 0) ? y(p) : t
        }
        document.onclick = i
        document.ontouchstart = i
        i()
      },
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #home {
    overflow-y: hidden;
  }

  #home-1 {
    margin: 0 auto;
    margin-top: 15%;
    text-align: center;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.647058823529412);
    font-family: '微軟正黑體 Bold', '微軟正黑體';
    font-weight: 700;
    font-style: normal;
    color: black;
    z-index: 10;
  }

  #home-cartoon {
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    z-index: -1;
  }

</style>



